@import './scss/variables';

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.button-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
}

.app-container {
  background-color: white;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 1fr;
  grid-template-areas:
  "board buttons"
  "board input";
  height: 404px;
  grid-gap: 40px;
}

.app-buttons {
  grid-area: buttons;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.app-board {
  grid-area: board;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-input {
  grid-area: input;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 78px;
}

@media screen and (max-width: $mobile-responsive) {
  .container {
    justify-content: center;
    height: 90vh;
    margin-top: 30px;
  }

  .app-container {
    justify-content: space-around;
    display: flex;
    flex-direction: column;
    height: 500px;
  }

  .app-input {
    margin-top: 60px;
  }
}

@media screen and (max-width: $small-mobile-responsive) {
  .app-container {
    grid-gap: 5px;
  }

  .button-container {
    width: 90%;
  }

  .app-input {
    margin-top: 40px;
  }
}
